
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import service from "@/uilt/request"
import {orderList} from "@/api/order"
export default {
components: {
Panel,
Group,
Radio
},
watch:{
type:function(old,news){
this.getData();
}
},
methods: {
onImgError (item, $event) {
console.log(item, $event)
},
getData(){
this.$vux.loading.show({text: 'Loading'});
var dataset=this;

orderList(this.pageindex,6,this.userid,this.type).then(function(result){
dataset.$vux.loading.hide();
if(result.data.length>0){
dataset.pageindex++;
dataset.title="当前拥有 "+result.count+" 条订单数据";
for(var i=0;i<result.data.length;i++){
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
}else{
dataset.$vux.toast.show({text: '没有更多数据了'});



}
}).catch(function(result){
console.log(result);
dataset.$vux.loading.hide();
alert("获取超时")
});
},
CrateItem(order){
var t= {desc: "",meta: {source: '来源信息',date: '时间',other: ''},data:null};
t.desc= "从"+order.startlocation+"到"+order.endlocation+"的订单.";
t.meta.source="价格:"+order.driverprice;
t.meta.date=order.startTime.split('+')[0].replace("T"," ");
t.data=order;
return t;
},
commit(item){
console.log(item);
alert("接单");
},
onfooter(){
this.getData();
}
},
data () {
return {
list: [],
title:"",
footer: {
title: "获取更多..."
},
pageindex:0
}
},
created(){
this.getData();
},
props:["type","userid"]
}



